exports.push([module.id, "\n.modifyDialog .el-dialog__header {\n  border-bottom: 1px solid rgba(230, 230, 235, 1);\n  padding-bottom: 20px;\n}\n.el-table th {\n  text-align: center;\n}\n.el-table__expanded-cell {\n  padding: 0 !important;\n  border-bottom: none;\n}\n", "", {"version":3,"sources":["E:/仲达集团/总后台管理系统/Zht-master/src/components/Serviceitem/src/components/Serviceitem/Serviceitem.vue"],"names":[],"mappings":";AA6PA;EACA,gDAAA;EACA,qBAAA;CACA;AACA;EACA,mBAAA;CACA;AACA;EACA,sBAAA;EACA,oBAAA;CACA","file":"Serviceitem.vue","sourcesContent":["<template>\n  <div>\n    <ctbHead>\n      <router-link :class=\"seCurId==item.id? 'cur':''\"\n                   v-for=\"item in authList\"\n                   :key=\"item.id\"\n                   :to=\"{path:item.action,query:{id:curId}}\"\n                   :v-key=\"item.id\">\n        {{item.name}}\n      </router-link>\n    </ctbHead>\n    <div class=\"container\">\n      <div class=\"quote\">\n        <div class=\"quote-ele\">\n          <i></i>\n          <span>会员特权服务项</span>\n          <el-button class='add'\n                     type=\"primary\"\n                     @click=\"modifyDialogVisible=true,addState=1\">添加</el-button>\n        </div>\n        <div class=\"quote-nav\">\n          <router-link :class=\"thCurId==item.id? 'cur':''\"\n                       v-for=\"item in threeAuthList\"\n                       :key=\"item.id\"\n                       :to=\"{path:item.action,query:{id:curId}}\">\n            {{item.name}}\n          </router-link>\n        </div>\n      </div>\n      <div>\n        <table class='table serviceTab'>\n          <tr>\n            <th>ID</th>\n            <th>服务项名称</th>\n            <th>产品个数</th>\n            <th>创建时间</th>\n            <th>操作</th>\n          </tr>\n          <tr v-for=\"item in list\">\n            <td>{{item.id}}</td>\n            <td>{{item.ser_name}}</td>\n            <td>{{item.pro_num}}</td>\n            <td>{{item.create_time}}</td>\n            <td>\n              <a href=\"javascript:;\"\n                 @click='addState=2,modifyDialogVisible=true,modify(item.id,item.ser_name)'\n                 class='col-0ACD00'>修改</a>\n              <a href=\"javascript:;\"\n                 @click=\"del(item.id,item.pro_num,item.ser_name)\"\n                 class='col-FA644B'>删除</a>\n            </td>\n          </tr>\n        </table>\n      </div>\n      <el-dialog :title=\"addState==1?'添加':'修改'\"\n                 class='modifyDialog'\n                 :visible.sync=\"modifyDialogVisible\"\n                 width=\"30%\"\n                 center>\n        <div>\n          <el-form ref=\"form\"\n                   :model=\"form\"\n                   label-width=\"100px\">\n            <el-form-item label=\"服务项名称 : \">\n              <el-input v-model=\"form.ser_name\"></el-input>\n            </el-form-item>\n          </el-form>\n        </div>\n        <span slot=\"footer\"\n              class=\"dialog-footer\">\n          <el-button @click=\"addService\"\n                     type=\"primary\">{{addState==1?'添加':'修改'}}</el-button>\n        </span>\n      </el-dialog>\n      <!-- 分页 -->\n      <div class=\"page_center\">\n        <paging :page-count=\"pageCount\"\n                :page=\"page\"\n                @index=\"paging\"></paging>\n      </div>\n    </div>\n  </div>\n</template>\n<script type=\"text/ecmascript-6\">\nimport page from '../common.js';\nexport default {\n  data () {\n    return {\n      list: [],\n      token: window.sessionStorage.getItem('bbytoken'),\n      obj: {},\n      reason: '',\n      reg_reason: '',\n      aid: '',\n      //分页数据\n      page: 1, //显示的页码数\n      totalpage: 1, //总页数\n      pageCount: 1,\n      index: 1, //点击的当前页，\n      endpage: 1,//当前一组页码的最大值\n      arr: [], //默认显示 1 2 3 4 5页\n      n: 1, //当前点击的页码的位置\n      authList: [],\n      curId: 0,\n      threeAuthList: [],\n      seCurId: 0,\n      thCurId: 0,\n      company: '', //输入的公司名称\n      modifyDialogVisible: false,  //修改弹框状态\n      DialogState: 1, //控制弹框的状态\n      currentID: 0,//当前列表id\n      addState: 1,\n      form: {\n        token: window.sessionStorage.getItem('bbytoken'),\n        ser_name: ''\n      }\n    }\n  },\n\n  created () {\n    this.init();\n  },\n  methods: {\n    paging (e) {\n      this.page = e;\n      this.init()\n    },\n    init () {\n      this.$axios.post('admin/SystemSetup/serItem', { token: this.token, page: this.page })\n        .then(res => {\n\n          if (res.data.code == 1) {\n            this.list = res.data.data.list\n            this.pageCount = res.data.data.rows;\n          } else {\n            this.list = []\n          }\n\n        }).catch(err => { })\n    },\n    addService () { //添加修改服务项\n      this.$axios.post('admin/SystemSetup/addSer', this.form).then(res => {\n        if (res.data.code == 1) {\n          layer.msg(res.data.msg, { time: 1500 }, (() => {\n            this.modifyDialogVisible = false\n            this.init()\n            this.form.ser_name = ''\n            this.form.id = ''\n          }))\n        } else {\n          layer.msg(res.data.msg, { time: 1500 })\n        }\n      }).catch(err => console.log(err))\n    },\n    //修改服务项\n    modify (id, ser_name) {\n\n      this.form = {\n        id: id,\n        ser_name: ser_name,\n        token: this.token\n      }\n    },\n    del (id, pro_num, ser_name) { //删除服务项\n      this.$confirm(`此服务项下产品有${pro_num}个,您确定删除吗?`, {\n        confirmButtonText: '确定',\n        cancelButtonText: '取消',\n        type: 'warning'\n      }).then(() => {\n\n        this.$axios.post('admin/SystemSetup/serdel ', { token: this.token, id: id, ser_name: ser_name }).then(res => {\n          if (res.data.code == 1) {\n            layer.msg(res.data.msg, { time: 1000 }, (() => {\n              this.init()\n            }))\n          } else {\n            layer.msg(res.data.msg, { time: 1000 })\n          }\n        }).catch(err => console.log(err))\n      }).catch(() => {\n\n      })\n    },\n\n  },\n  mounted () {\n\n\n    var id = this.$route.query.id;\n    this.curId = id;\n    this.$axios.post('admin/Auth/erAuth', {\n      token: window.sessionStorage.getItem('bbytoken'),\n      id: id\n    })\n      .then(res => {\n\n        if (res.data.code == 1) {\n          var arr = res.data.data;\n          for (var i = 0; i < arr.length; i++) {\n            if (arr[i].name == '半价服务') {\n              this.seCurId = arr[i].id;\n              this.threeAuthList = arr[i].son;\n\n              for (var j = 0; j < arr[i].son.length; j++) {\n\n                if (arr[i].son[j].name == \"服务项列表\") {\n                  this.thCurId = arr[i].son[j].id\n                }\n              }\n            }\n          }\n          this.authList = arr;\n        } else {\n          this.$alert(res.data.msg, '提示', { type: 'error' });\n        }\n      })\n      .catch(err => {\n        alert(err);\n      })\n  },\n}\n</script>\n<style scoped>\n.demo-table-expand {\n  font-size: 0;\n}\n.demo-table-expand label {\n  width: 90px;\n  color: #99a9bf;\n}\n\n.addbtn {\n  float: right;\n  margin-right: 100px;\n  margin-top: 20px;\n}\n.serviceTab th {\n  width: 20%;\n}\n.serviceTab a {\n  margin-left: 10px;\n}\n.div {\n  word-wrap: break-word;\n  word-break: normal;\n}\n.add {\n  float: right;\n  margin-top: 20px;\n  margin-right: 50px;\n}\n</style>\n<style>\n.modifyDialog .el-dialog__header {\n  border-bottom: 1px solid rgba(230, 230, 235, 1);\n  padding-bottom: 20px;\n}\n.el-table th {\n  text-align: center;\n}\n.el-table__expanded-cell {\n  padding: 0 !important;\n  border-bottom: none;\n}\n</style>\n"],"sourceRoot":""}]);

